@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21.5px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background: rgba(3, 16, 27, 0.7);
}

.modal-container--close {
  display: none;
}

.modal-container__modal {
  position: relative;
  margin: auto;
  padding: 40px;
  background: #00b3ff;
  border-radius: 3px;
}

.header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}

.header__container {
  display: flex;
  align-items: center;
  padding: 27px 20px 35px 20px;
  max-width: 1240px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .header__container {
    padding: 20px 50px 25px 50px;
    max-width: 1023px;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding: 15px 35px 20px 26px;
    max-width: 1023px;
    justify-content: space-between;
  }
}

.header__logo {
  margin-right: 160px;
}
@media (max-width: 1023px) {
  .header__logo {
    margin: 0;
  }
}

.header__logo a {
  outline: none;
}
.header__logo a:focus {
  outline: 1px solid #358cbe;
}

.header__user-nav {
  margin-right: 147px;
}
@media (max-width: 1023px) {
  .header__user-nav {
    margin: 0;
  }
}

.header__button {
  box-sizing: border-box;
  cursor: pointer;
  outline: 1px solid #f2f5f7;
  border-radius: 3px;
  padding: 0;
}
.header__button--order-call {
  width: 160px;
  height: 40px;
  background: transparent;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f7;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}
.header__button:hover {
  background: #358cbe;
}
.header__button:focus {
  outline: 1px solid #358cbe;
}

.logo {
  padding: 1px 0 0 0;
  width: 215px;
  display: flex;
}
@media (max-width: 767px) {
  .logo {
    width: 35px;
  }
}
@media (max-width: 1023px) {
  .logo {
    margin: 0;
  }
}

@media (max-width: 767px) {
  .logo img {
    width: 35px;
    height: 35px;
  }
}

.user-nav {
  max-width: 480px;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .user-nav {
    max-width: 100px;
  }
}

.user-nav__list {
  display: flex;
  margin: 0;
  padding: 0;
}

.user-nav__list li {
  display: flex;
}
.user-nav__list li:not(:last-child) {
  margin-right: 28px;
}
@media (max-width: 1023px) {
  .user-nav__list li:not(:last-child) {
    margin-right: 20px;
  }
}

.user-nav a {
  width: 225px;
  display: flex;
  padding: 12px 0 12px 0;
  outline: none;
}
.user-nav a:hover {
  background: rgba(10, 48, 94, 0.6);
}
.user-nav a:focus {
  outline: 1px solid #358cbe;
}
@media (max-width: 1023px) {
  .user-nav a {
    width: 40px;
    display: block;
  }
}

.user-nav__link-icon {
  margin-right: 15px;
}

.user-nav span {
  line-height: 18px;
  color: #f2f5f7;
}
@media (max-width: 1023px) {
  .user-nav span {
    display: none;
  }
}

.modal {
  width: 500px;
}

.modal__close-button {
  position: absolute;
  padding: 0;
  top: 15px;
  right: 15px;
  width: 40px;
  height: 40px;
  border: none;
  background: transparent;
  background-image: url("../img/svg/modal-close.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  outline: none;
  transition: 0.3s ease-in;
}
.modal__close-button:hover {
  transform: rotate(360deg);
}
.modal__close-button:focus {
  outline: 1px solid #041427;
}

.modal__content h2 {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 140.62%;
  color: #ffffff;
}

.modal__content p {
  margin: 0;
  padding: 15px 20px 0 0;
  line-height: 22px;
  color: #ffffff;
}

.form-call {
  padding: 40px 0 0 0;
}

.form-call__field input {
  border: none;
  background: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  padding: 15px;
  width: 100%;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 20px;
  outline: none;
  color: #ffffff;
}
.form-call__field input::placeholder {
  color: #ffffff;
}
.form-call__field input:hover {
  background: rgba(22, 64, 116, 0.55);
}
.form-call__field input:active {
  background: rgba(40, 101, 175, 0.55);
}
.form-call__field input:focus {
  outline: 1px solid #041427;
}

.form-call__field-description textarea {
  border: none;
  background: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  padding: 15px;
  width: 100%;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 25px;
  outline: none;
}
.form-call__field-description textarea::placeholder {
  color: #ffffff;
}
.form-call__field-description textarea:hover {
  background: rgba(22, 64, 116, 0.55);
}
.form-call__field-description textarea:focus {
  outline: 2px solid #041427;
}

.form-call__button-submit {
  border: none;
  background: #041427;
  border-radius: 3px;
  padding: 15px 60px;
  width: 100%;
  margin-bottom: 20px;
  cursor: pointer;
  outline: none;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
}
.form-call__button-submit:focus {
  outline: 1px solid #ffffff;
}

.form-call__input-check label {
  cursor: pointer;
  padding: 0 0 0 25px;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
}

.form-call__input-check input {
  position: absolute;
  appearance: none;
  outline: none;
}
.form-call__input-check input:checked + span {
  background: url("../../img/svg/check-yes.svg");
  background-repeat: no-repeat;
  background-position: 50% 45%;
  background-size: 10px 7px;
}

.form-call__input-check span {
  margin: 0 0 0 -20px;
  position: absolute;
  box-sizing: border-box;
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
}

.footer {
  background: #041827;
}

.footer__container {
  display: flex;
  max-width: 1240px;
  margin: 0 auto;
  padding: 50px 20px 55px 20px;
}
@media (max-width: 1023px) {
  .footer__container {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 50px 45px 65px 50px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    padding: 0;
  }
}

.footer__logo-wrapper {
  margin-right: auto;
}
@media (max-width: 1023px) {
  .footer__logo-wrapper {
    display: flex;
    justify-content: space-between;
    margin: 0 0 35px 0;
  }
}
@media (max-width: 767px) {
  .footer__logo-wrapper {
    padding: 35px 0 0 0;
    flex-direction: column;
  }
}

@media (max-width: 767px) {
  .footer__logo {
    width: 215px;
    margin: 0 auto 15px;
  }
}
.footer__logo:focus {
  outline: 1px solid #358cbe;
}

.footer__logo img {
  width: 215px;
  height: 55px;
  margin-bottom: 30px;
  cursor: pointer;
  outline: none;
}
@media (max-width: 1023px) {
  .footer__logo img {
    margin-bottom: 0;
  }
}

.footer__section-wrapper {
  display: flex;
}
@media (max-width: 1023px) {
  .footer__section-wrapper {
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .footer__section-wrapper {
    flex-direction: column;
    border-top: 1px solid rgba(248, 253, 255, 0.7);
  }
}

.footer__section-site {
  margin-right: 30px;
}
@media (max-width: 767px) {
  .footer__section-site {
    margin: 0;
  }
}

@media (max-width: 767px) {
  .footer__address {
    border-top: 1px solid rgba(248, 253, 255, 0.7);
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
    margin-bottom: 35px;
  }
}

.social {
  list-style-type: none;
  margin: 0;
  padding: 0 0 0 10px;
  width: 160px;
  display: grid;
  grid-template-columns: repeat(3, 40px);
  column-gap: 15px;
}
@media (max-width: 1023px) {
  .social {
    width: 150px;
    align-items: center;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .social {
    margin: 0 auto;
  }
}

.social__item a {
  display: block;
  padding: 5px;
  width: 40px;
  height: 40px;
  outline: none;
  fill: #f8fdff;
}
.social__item a:hover {
  fill: #5db5e8;
}
.social__item a:active {
  fill: #286c93;
}
.social__item a:focus {
  outline: 1px solid #358cbe;
}

.section-site__wrapper {
  display: flex;
  gap: 30px;
}
@media (max-width: 1023px) {
  .section-site__wrapper {
    flex-direction: column;
    gap: 0;
  }
}
@media (max-width: 767px) {
  .section-site__wrapper {
    padding: 0 25px 20px 25px;
  }
}
@media (max-width: 767px) {
  .section-site__wrapper--close {
    display: none;
  }
}

@media (max-width: 767px) {
  .section-site__control-title {
    display: flex;
    justify-content: space-between;
    padding: 20px 26px 20px 25px;
  }
}

.section-site__button {
  display: none;
  background-color: transparent;
  border: none;
  padding: 0;
  width: 15px;
  height: 15px;
}
@media (max-width: 767px) {
  .section-site__button--open {
    display: block;
    background: url("../img/svg/minus.svg") no-repeat;
  }
  .section-site__button--close {
    display: block;
    background: url("../img/svg/plus.svg") no-repeat;
  }
}

.section-site__control-title h2 {
  position: relative;
  margin: 0 0 20px 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
}
@media (max-width: 767px) {
  .section-site__control-title h2 {
    margin: 0;
  }
}

.section-site__list {
  margin: 0;
  padding: 0;
  width: 220px;
}

.section-site__item:not(:last-child) {
  margin: 0 0 10px 0;
}
@media (max-width: 1023px) {
  .section-site__item {
    margin: 0 0 10px 0;
  }
}

.section-site__item a {
  display: block;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
  transition: 0.2s ease-in;
}
@media (max-width: 767px) {
  .section-site__item a {
    font-size: 14px;
    line-height: 25px;
  }
}
.section-site__item a:hover {
  color: #358cbe;
}
.section-site__item a:active {
  color: #ffffff;
}
.section-site__item a:focus {
  outline: 1px solid #358cbe;
}

.address__wrapper {
  width: 320px;
}
@media (max-width: 767px) {
  .address__wrapper {
    padding: 0 25px 20px 25px;
  }
  .address__wrapper--close {
    display: none;
  }
}

.address__control-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .address__control-title {
    padding: 20px 27px 15px 25px;
  }
}

.address__control-button {
  display: none;
  background-color: transparent;
  border: none;
  padding: 0;
  width: 15px;
  height: 15px;
}
@media (max-width: 767px) {
  .address__control-button--open {
    display: block;
    background: url("../img/svg/minus.svg") no-repeat;
  }
  .address__control-button--close {
    display: block;
    background: url("../img/svg/plus.svg") no-repeat;
  }
}

.address__control-title h2 {
  position: relative;
  margin: 0 0 20px 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
}
@media (max-width: 767px) {
  .address__control-title h2 {
    margin: 0;
  }
}

.address address {
  font-style: normal;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 10px;
}
@media (max-width: 767px) {
  .address address {
    font-size: 14px;
    line-height: 135%;
    margin-bottom: 15px;
  }
}

.address__phone-list {
  margin: 0;
  padding: 0;
}

.address__phone-item:not(:last-child) {
  margin-bottom: 10px;
}

.address__phone-item a {
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 767px) {
  .address__phone-item a {
    font-size: 14px;
    line-height: 25px;
  }
}
.address__phone-item a:hover {
  color: #358cbe;
}
.address__phone-item a:active {
  color: #286c93;
}
.address__phone-item a:focus {
  outline: 1px solid #358cbe;
}

